:root {
  --primary-color: #8e3200;
  --secondary-color: #d9a566;
  --text-color: #333;
  --background-color: #fff8f0;
  --accent-color: #e76f51;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat, Arial, sans-serif;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  line-height: 1.6;
}

header {
  background-color: var(--primary-color);
  color: #fff;
  text-align: center;
  padding: 1rem 0;
  box-shadow: 0 2px 5px #0000001a;
}

.logo {
  margin-bottom: .5rem;
  font-size: 2rem;
  font-weight: bold;
}

nav {
  background-color: var(--secondary-color);
  padding: 1rem 0;
}

nav ul {
  justify-content: center;
  list-style-type: none;
  display: flex;
}

nav ul li {
  margin: 0 1rem;
}

nav ul li a {
  color: var(--text-color);
  font-weight: bold;
  text-decoration: none;
  transition: color .3s;
}

nav ul li a:hover {
  color: var(--primary-color);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.hero {
  text-align: center;
  margin-bottom: 3rem;
}

.hero h1 {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.hero p {
  max-width: 800px;
  margin: 0 auto;
  font-size: 1.2rem;
}

.featured-recipes {
  margin-bottom: 3rem;
}

.section-title {
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 2rem;
}

.recipe-grid {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  display: grid;
}

.recipe-card {
  background-color: #fff;
  border-radius: 10px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 3px 10px #0000001a;
}

.recipe-card:hover {
  transform: translateY(-5px);
}

.recipe-image {
  background-color: #ddd;
  background-position: center;
  background-size: cover;
  height: 200px;
}

.recipe-content {
  padding: 1.5rem;
}

.recipe-title {
  color: var(--primary-color);
  margin-bottom: .5rem;
  font-size: 1.3rem;
}

.recipe-movie {
  color: var(--accent-color);
  margin-bottom: .5rem;
  font-style: italic;
}

.recipe-description {
  margin-bottom: 1rem;
}

.recipe-link {
  background-color: var(--primary-color);
  color: #fff;
  border-radius: 5px;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.recipe-link:hover {
  background-color: var(--accent-color);
}

.categories {
  margin-bottom: 3rem;
}

.category-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.category-item {
  background-color: var(--secondary-color);
  cursor: pointer;
  border-radius: 20px;
  padding: .7rem 1.5rem;
  transition: background-color .3s;
}

.category-item:hover {
  background-color: var(--primary-color);
  color: #fff;
}

footer {
  background-color: var(--primary-color);
  color: #fff;
  text-align: center;
  margin-top: 3rem;
  padding: 2rem 0;
}

.social-icons {
  margin-top: 1rem;
}

.social-icons span {
  margin: 0 .5rem;
  font-size: 1.5rem;
}

@media (width <= 768px) {
  .recipe-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }

  nav ul {
    flex-direction: column;
    align-items: center;
  }

  nav ul li {
    margin: .5rem 0;
  }
}
